import React, { useEffect, useState } from "react";
import OpenSeaDragon from "openseadragon";
import "./opensea-dragon-svg-overlay";
import * as d3 from "d3";
import useKeyPress from "./UseKeyPress";

window.shouldQuit = false

const OSG = (props) => {

  // Create state variables for viewer, overlay and d3 nodes (svg)
  const [imageInfo, setImageInfo] = useState(null)
  const [viewer, setViewer] = useState(null)
  const [overlay, setOverlay] = useState(null)
  const [d3Node, setd3Node] = useState(null)
  const [dataset, setDataset] = useState(null)

  const getImageInfo = () => {
    fetch(`http://${props.host}:${props.port}/image_info.json`)
      .then(function (response) {
        return response.json();
      })
      .then(function (myJson) {
        setImageInfo(myJson);
      });
  };

  // load image info and data on render
  useEffect(() => {
      getImageInfo()
      getData()
  }, [])

  const getData = () => {
    fetch(`http://${props.host}:${props.port}/data_Visium.json`)
      .then(function (response) {
        return response.json();
      })
      .then(function (myJson) {
        setDataset(myJson);
      });
  };

 // Function to initiate the OpenSeaDragon viewer
 const InitOpenseadragon = () => {
  viewer && viewer.destroy();
  setViewer(
    OpenSeaDragon({
      id: "openseadragon",
      tileSources: {
        height: imageInfo.image_height,
        width: imageInfo.image_width,
        tileSize: imageInfo.tilesize,
        minLevel: imageInfo.minZoomLevel,
        maxLevel: imageInfo.maxZoomLevel,
        getTileUrl: (level, x, y) => {
          return `http://${props.host}:${props.port}/tiles/${level}/${x}_${y}.jpg`;
        }
      },
      defaultZoomLevel: imageInfo.minZoomLevel,
      minZoomLevel: imageInfo.minZoomLevel,
      maxZoomLevel: imageInfo.maxZoomLevel + 2,
      visibilityRatio: 1.0,
      minZoomImageRatio: 1.0,
      showNavigationControl: false
    })
  );
 };

 // Initiate a new osd viewer once image info has been loaded
 useEffect(() => {
  if (imageInfo) {
    InitOpenseadragon();
  }
  return () => {
    viewer && viewer.destroy();
  };
  // eslint-disable-next-line react-hooks/exhaustive-deps
}, [imageInfo]);

// Add a svg overlay to the osd viewer after the viewer is initialized
useEffect(() => {
  if (viewer) {
    setOverlay(viewer.svgOverlay());
  }
}, [viewer]);


// Create d3 nodes for the overlay
useEffect(() => {
  if (overlay) {
    setd3Node(d3.select(overlay.node()));
  }
}, [overlay]);

// Draw spots on the svg overlay
useEffect(() => {
  if (dataset) {
    //console.log(dataset);

    // Draw links between spots
    d3Node
      .selectAll("line")
      .data(dataset.links)
      .enter()
      .append('svg:line')
      .attr("x1", function (d) {
        return d.x;
      })
      .attr("y1", function (d) {
        return d.y;
      })
      .attr("x2", function (d) {
        return d.x_end;
      })
      .attr("y2", function (d) {
        return d.y_end;
      })
      .attr("shape-rendering", "optimizeSpeed")
      .attr("style", function(d) {
        if (d.keep) {
          return "stroke: black"
        } else {
          return "stroke: red"
        }
      })
      .style('stroke-width', 0.001)
      .on("mouseover", function (e) {
        if (e.shiftKey) {
          const obj = e.srcElement.__data__;
          let copy = dataset;
          copy.links[obj.index - 1].keep = false;
          setDataset(copy);
          d3.select(this)
            .style("stroke", "red")
        }
        if (e.ctrlKey) {
          const obj = e.srcElement.__data__;
          let copy = dataset;
          copy.links[obj.index - 1].keep = true;
          setDataset(copy);
          d3.select(this)
            .style("stroke", "black")
            .attr("remove", false)
        }
      })

    // draw circles for each spot
    d3Node
      .selectAll("circle")
      .data(dataset.nodes)
      .enter()
      .append("g")
      .append("circle")
      .attr("cx", function (d) {
        return d.x;
      })
      .attr("cy", function (d) {
        return d.y;
      })
      .attr("shape-rendering", "optimizeSpeed")
      .attr("r", 0.003)
      .style("fill", "white")
      .attr("opacity", function (d) {
        return 0.8;
      })

  }
}, [dataset, d3Node]);

 // create a key press
 const shiftPress = useKeyPress("Shift");
 const ctrlPress = useKeyPress("Control");

 useEffect(() => {
  if (shiftPress) {
    document.getElementById("openseadragon").style.cursor = "url('data:image/x-icon;base64,'),auto"
  } else if (ctrlPress) {
    document.getElementById("openseadragon").style.cursor = "url('data:image/x-icon;base64,'), auto";
  } else {
    document.getElementById("openseadragon").style.cursor = "default";
  }
 }, [shiftPress, ctrlPress]);

  // Listen to changes in window.shouldQuit
  useEffect(() => {
    if (props.quit) {
      window.savedData = dataset
    }
  }, [props.quit, dataset])

  return (
    <>
      <div
        id="openseadragon"
        style={{
          height: props.height,
          width: props.width,
        }}
      ></div>
    </>
  )
};

export { OSG };
